// BASIC
import React, { Component } from 'react'
import styled, {css, keyframes} from 'styled-components'
import {au, db} from '../../../Config/firebase'
import moment from 'moment'
// ICONS
import {MdSend} from 'react-icons/md'
// IMAGES
import logo from '../../Images/logo/easyword.svg'
import logoIcon from '../../Images/logo.png'

moment.locale('pl')

const ChatSite = styled.div`
	width: 100vw;
	max-width: 1000px;
	height: 100vh;
	height: calc(var(--vh, 1vh) * 100);
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 0 auto;
	padding: 30px;
`
const Friend = styled.div`
	width: 100%;
	height: 50px;
	display: flex;
	align-items: center;
	margin-bottom: 20px;
`
const Avatar = styled.div`
	width: 50px;
	height: 50px;
	background: url("${props => props.src}") no-repeat center center;
	background-size: cover;
	border-radius: 100%;
	margin-right: 10px;
`
const Nick = styled.strong`
	font-size: 20px;
`
const Messages = styled.div`
	position: relative;
	width: 100%;
	height: calc(100vh - 100px);
	height: calc(var(--vh, 1vh) * 100 - 170px);
	display: flex;
	flex-direction: column-reverse;
	align-items: flex-start;
	overflow-y: scroll;
	scroll-behavior: smooth;
	::-webkit-scrollbar {
		width: 0;
	}
`
const Time = styled.div`
	position: absolute;
	right: -50px;
	top: 50%;
	transform: translateY(-50%);
	font-size: 15px;
	color: var(--color-secondary);
	opacity: 0;
	transition: opacity 0.2s ease;
`
const DateSent = styled.div`
	display: none;
	font-size: 12px;
	margin-top: 5px;
`
const fade = keyframes`
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
`
const Message = styled.div`
	position: relative;
	max-width: 80%;
	background-color: var(--chat-decorative);
	padding: 8px 13px;
	margin: 5px 0;
	border-radius: 20px;
	transition: all 0.1s ease;
	transform-origin: left;
	animation: ${fade} 0.2s ease;
	:active {
		transform: scale(0.95);
	}
	@media (hover: hover) {
		:hover {
			cursor: pointer;
		}
	}
	${props =>
		props.owner === 'true' &&
		css`
			background-color: var(--chat-bg);
			margin-left: auto;
			transform-origin: right;
			${DateSent} {
				text-align: right;
			}
			${Time} {
				left: -50px;
			}
		`
	};
`
const Sticker = styled.div`
	position: relative;
	width: 150px;
	min-height: 150px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin: 5px 0;
	background-color: var(--color-dark);
	border-radius: 20px;
	transition: all 0.1s ease;
	transform-origin: left;
	animation: ${fade} 0.2s ease;
	:active {
		transform: scale(0.95);
	}
	@media (hover: hover) {
		:hover {
			cursor: pointer;
		}
	}
	${props =>
		props.owner === 'true' &&
		css`
			margin-left: auto;
			transform-origin: right;
			background-color: var(--color-dark);
			${DateSent} {
				text-align: right;
			}
			${Time} {
				left: -50px;
			}
		`
	};
`
const StickerImg = styled.img`
	width: 80%;
	height: 80%;
`
const Menu = styled.div`
	width: 100%;
	max-width: 1000px;
	position: fixed;
	bottom: 20px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 20px;
`
const Send = styled(MdSend)`
	min-width: 30px;
	color: var(--color-main);
	font-size: 30px;
	margin-left: 20px;
	@media (hover: hover) {
		transition: all 0.1s ease;
		:hover {
			cursor: pointer;
			transform: scale(1.1);
		}
	}
`
const SendSticker = styled.img`
	width: 40px;
	height: 40px;
	margin-left: 10px;
	@media (hover: hover) {
		transition: all 0.1s ease;
		:hover {
			cursor: pointer;
			transform: scale(1.1);
		}
	}
`
const Input = styled.input`
	width: calc(100% - 0px);
	border: none;
	background-color: var(--chat-bg);
	padding: 8px 13px;
	margin: 5px 0;
	border-radius: 30px;
	font-size: 18px;
	::placeholder {
		color: var(--color-primary);
		transition: all 0.2s ease;
	}
	:focus {
		::placeholder {
			opacity: 0;
			transform: translateX(20px);
			letter-spacing: 5px;
		}
	}
`
const rotate = keyframes`
	from {
		transform: rotate(0)
	}
	to {
		transform: rotate(360deg)
	}
`
const Loader = styled.div`
	margin: 0 auto;
	font-size: 20px;
	font-weight: bold;
	color: var(--color-main);
	letter-spacing: 1px;
	${props =>
		props.hide === "true" &&
		css`
			opacity: 0;
		`
	};
`

class Chat extends Component {
	state = {
		changeLimit: true,
		scroll: true,
		limit: 20,
		users: [],
		userId: '',
		content: [],
		message: '',
		lastId: 0
	}
	componentDidMount() {
		au.onAuthStateChanged(user => {
			if (user) {
				db.collection('chats').doc(this.props.match.params.chatId).get().then(snap => {
					if(snap.data().users.includes(user.uid)) {
						this.setState({
							userId: user.uid,
							users: snap.data().users
						})
						const friends = snap.data().users.filter((uid) => uid !== user.uid)
						db.collection('users').doc(friends[0]).get().then(snap => {
							this.setState({friend: snap.data().info})
						})
						db.collection('chats').doc(this.props.match.params.chatId).collection('content').orderBy('createdAt', 'desc').limit(this.state.limit).onSnapshot(snaps => {
							const content = []
							snaps.forEach(snap => {
								content.push(snap.data())
							})
							this.setState({
								lastId: content.length ? Number(content[0].id) : 0,
								content: content,
								changeLimit: content.length < (content.length ? Number(content[0].id) : 0)
							})
						})
					}
				})
			}
		})
	}
	componentDidUpdate() {
		if (this.state.content.length !== 0 && this.state.scroll) {
			document.getElementById((this.state.content[0]).id.toString().padStart(10, '0')).scrollIntoView({ behavior: 'smooth', block: 'end' });
		}
	}
	sendMessage = () => {
		const newId = (this.state.lastId + 1).toString().padStart(10, '0')
		this.setState({scroll: true})
		if (this.state.message.trim()) {
			const data = {
				content: this.state.message.trim(),
				createdAt: new Date(),
				uid: this.state.userId,
				id: newId
			}
			this.setState({message: ''})
			document.getElementById('message').value = '';
			document.getElementById('message').focus();
			db.collection('chats').doc(this.props.match.params.chatId).collection('content').doc(newId).set(data)
		}
		else if (!this.state.message) {
			const data = {
				type: 'logo',
				createdAt: new Date(),
				uid: this.state.userId,
				id: newId
			}
			db.collection('chats').doc(this.props.match.params.chatId).collection('content').doc(newId).set(data)
		}
	}
	setMessage = (e) => {
		e.persist()
		this.setState({scroll: true, message: e.target.value})
	}
	checkKey = (e) => {
		if (e.key === 'Enter') {
			this.sendMessage()
		}
	}
	increaseLimit = () => {
		this.setState(prevState => ({
			limit: prevState.limit + 20,
			scroll: false
		}), () => {
			db.collection('chats').doc(this.props.match.params.chatId).collection('content').orderBy('createdAt', 'desc').limit(this.state.limit).onSnapshot(snaps => {
				const content = []
				snaps.forEach(snap => {
					content.push(snap.data())
				})
				console.log(content.length, this.state.lastId);
				this.setState({
					content: content,
					changeLimit: content.length < this.state.lastId
				})
			})
		});

	}
	checkPosition = (e) => {
		e.persist()
		const bottom = Math.round(e.target.scrollTop) + 100 >= e.target.scrollHeight - e.target.clientHeight;
		if (bottom && this.state.scroll === false) {
			this.setState({scroll: true})
		}
		else if (!bottom && this.state.scroll === true) {
			this.setState({scroll: false})
		}
		if (e.target.scrollTop === 0 && this.state.changeLimit) {
			this.increaseLimit()
		}
	}
	showDate = (id) => {
		const element = document.getElementById(id).children[2];
		console.log(element);
		if (!element.style.display) {
			element.style.display = 'block'
		}
		else {
			element.style.display = ''
		}
	}
	showTime = (id) => {
		const element = document.getElementById(id).children[0];
		if (element.style.opacity === '0' || !element.style.opacity) {
			element.style.opacity = '1'
		}
	}
	hideTime = id => {
		const element = document.getElementById(id).children[0];
		if (element.style.opacity === '1') {
			element.style.opacity = '0'
		}
	}
	render() {
		return (
			<ChatSite>
				{/* <Loader><AiOutlineLoading /></Loader> */}
				{!this.state.userId ? '...' : <>
					<Friend>
						{this.state.friend && <>
							<Avatar src={this.state.friend.avatar} />
							<Nick>{this.state.friend.nick}</Nick>
						</>}
					</Friend>
					<Messages id="messages" onScroll={this.checkPosition}>
						{this.state.content.map(({content, createdAt, id, uid, type}) => {
							let date = new Date(1970, 0, 1)
							date.setSeconds(createdAt.seconds)
							if (!type) {
								return <Message
									onClick={() => this.showDate(id)}
									onMouseOver={() => this.showTime(id)}
									onMouseOut={() => this.hideTime(id)}
									owner={(this.state.userId === uid).toString()}
									key={id}
									id={id}
								>
									<Time>{moment(date).add(2, 'hours').format('LT')}</Time>
									<span>{content}</span>
									<DateSent>{moment(date).add(2, 'hours').format('LL')}</DateSent>
								</Message>
							}
							else if (type === 'logo') {
								return <Sticker
								onClick={() => this.showDate(id)}
								onMouseOver={() => this.showTime(id)}
								onMouseOut={() => this.hideTime(id)}
								owner={(this.state.userId === uid).toString()}
								key={id}
								id={id}
							>
								<Time>{moment(date).add(2, 'hours').format('LT')}</Time>
								<StickerImg src={logo} />
								<DateSent>{moment(date).add(2, 'hours').format('LL')}</DateSent>
							</Sticker>
							}
							else {
								return <Message
								owner={(this.state.userId === uid).toString()}
								key={id}
								id={id}
							>
								Wystapił błąd!
							</Message>
							}
						})}
						<Loader hide={(!this.state.changeLimit).toString()}>...</Loader>
					</Messages>
					<Menu>
						<Input placeholder="Wpisz wiadomość..." autoComplete="off" id="message" onChange={this.setMessage} onKeyPress={this.checkKey} />
						{this.state.message ? <Send onClick={this.sendMessage} /> : <SendSticker src={logo} onClick={this.sendMessage} />}
					</Menu>
				</>}
			</ChatSite>
		);
	}
}

export default Chat